:root {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}

body {
    padding: 0;
    margin: 0;
    
}

.App {
    height: 100vh;
}

#header-nav {
    display: grid;
    grid-template-columns: 5fr 8fr;
    justify-content: left;
    align-items: center;
    background-color: #3e5962;
    color: white;
    padding-left: 20px;
}

.yellow-title {
    color: #c0a02e;
}

#nav-list {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    align-items: center;
}

a, a:visited {
    text-decoration: none;
    color: white;
}

h1 {
    font-size: 50px;
}

h2 {
    font-size: 12px;
    font-weight:400;
}

li {
    font-size: 20px;
}

.underline {
    padding-bottom: 20px;
}

.underline:hover {
    color: #b8b8b8;
    border-bottom: 2px solid #e1dac6;
}

#shopping-cart{
    display:flex;
    align-items: center;
}

#number-cart {
    color:#3e5962;
    background: white;
    border-radius: 30px;
    width: 40px;
    height: 40px;
    text-align: center;
    display:flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
}

#homepage {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 250px;
    height: 80%;
}

p {
    font-size: 22px;
    width: 600px;
}

#book-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    gap: 50px;
}

.book-card {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid black; */
    padding: 10px;
    width: 100%;
}

.book-cover {
    flex: 10;
    padding: 10px;
}

.title-author-container {
    flex: 1;
    width: 200px;
    text-align: center;
}

.price {
    flex: 1;
    font-size: .75em;
    padding: 20px;
}

.quantity-form {
    display: flex;
    flex-direction: column;
}

.quantity {
    flex: 1;
    margin-bottom: 20px;
    text-align: center;
}

.add-to-cart, #checkout-button{
    flex: 1;
    width: 250px;
    height: 50px;
    background-color: #305c98;
    color: white;
    border: none;
}

.add-to-cart:hover, #checkout-button:hover {
    background-color: #234069;
}

.add-to-cart:active {
    border: 3px solid black;
}

#button-cart-container {
    text-align: center;
    display:grid;
    grid-template-columns: 1fr 10fr 5fr 3fr;
}

#side-bar {
    grid-column: 3 / 4;
    margin-top: 100px;
}

#checkout-button {
    margin-top: 10px;
}

#no-items {
    margin-top: 100px;
    font-size: 30px;
}

.shopping-cart-container {
    grid-column: 2 / 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.book-cart {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 50%;
    border-bottom: 1px solid black;
}

.cart-title, .cart-price, .cart-quantity, .cart-quantity-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cart-cover {
    height: auto;
    width: 150px;
}

.cart-quantity-input {
    width: 50%;
}